import MetaTags from "common/MetaTags";
import { Page } from "common/components";
import { useTranslation } from "react-i18next";
import { getDescription } from "../descriptions";
import AboutTurkeyAr from "./about-turkey-ar";
import AboutTurkeyEn from "./about-turkey-en";
import WhyTurkeyAr from "./why-turkey-ar";
import WhyTurkeyEn from "./why-turkey-en";

const Turkey = ({ contentId, titleId }) => {
  const [t, i18n] = useTranslation();

  const getTitle = () => t(titleId);

  const metaTags = [
    {
      name: "description",
      content: getDescription(contentId, i18n.language),
    },
  ];

  const renderSwitch = () => {
    switch (contentId) {
      case "about-turkey":
        return i18n.language === "ar" ? (
          <AboutTurkeyAr title={getTitle()} />
        ) : (
          <AboutTurkeyEn title={getTitle()} />
        );
      case "why-turkey":
        return i18n.language === "ar" ? (
          <WhyTurkeyAr title={getTitle()} />
        ) : (
          <WhyTurkeyEn title={getTitle()} />
        );
      default:
        return null;
    }
  };

  return (
    <Page title={getTitle()}>
      <MetaTags title={getTitle()} tags={metaTags} />
      <div className="content">
        <div className="container">
          <div className="row">
            <div className="col-12">{renderSwitch()}</div>
          </div>
        </div>
      </div>
    </Page>
  );
};

export default Turkey;
